/* harmony default export */ __webpack_exports__["default"] = ("<app-header></app-header>\r\n<div class=\"homeHeader\">\r\n  <div class=\"container\">\r\n    <h2>CAMPUS NUMERIQUES DE L'ESPACE UNIVERSITAIRE FRANCOPHONE</h2>\r\n  </div>\r\n</div>\r\n<img src=\"../assets/sigle_auf.png\" class=\"img-responsive sigle pull-right\">\r\n<br>\r\n <div  class=\"container\"><h3>C'est quoi un CNEUF</h3>\r\n<div class=\"container bodyuser\">\r\n  <div class=\"col-md-6\">\r\n    <p class=\"descrip\">\r\n    Au dbut des annes 1990, lAUF a soutenu la mise en place au sein\r\n    de ses universits membres dinfrastructures spcifiques, les\r\n    campus numriques francophones (CNF) qui permettaient aux acteurs\r\n    universitaires daccder via Internet  des bibliothques\r\n    numriques,  des formations diplmantes et  distance, et \r\n    des parcours de soutien aux formateurs dans les domaines des\r\n    TICE. De nouveaux besoins du monde universitaire ont ensuite\r\n    conduit  un largissement des champs dactivit de proximit.\r\n    Les nouveaux campus numriques francophones  CNEUF  reposent\r\n    sur un agencement des trois dimensions de lacquisition des\r\n    comptences : acquisition des savoirs, acquisition des savoir-faire,\r\n    acquisition des savoir-tre.\r\n    </p>\r\n    <br>\r\n    <p>\r\n      <b>\r\n        Ainsi, les CNEUF offrent aujourdhui un environnement de travail privilgi avec la mise  disposition :\r\n      </b>\r\n    </p>\r\n    <br>\r\n    <p  class=\"descrip\">\r\n      <li>Dinfrastructures techniques et de rseau de qualit pour se former, se diplmer  distance ou encore accder  des ressources documentaires, pdagogiques, scientifiques, culturelles.</li>\r\n      <li>Despace de lieu de travail partag (coworking) pour travailler et partager ses comptences.</li>\r\n      <li>Daccompagnement dans la maturation de projets de fabrication numriques.</li>\r\n    </p>\r\n  </div>\r\n\r\n  <div class=\"col-md-6\">\r\n    <div class=\"rightside\">\r\n    <img src=\"../assets/CNEUF-BRAZAVILLE.jpg\" class=\"img-responsive\">\r\n    <br><br>\r\n    <strong>LAUF soutient la mise en place de CNEUF via des appels  projets</strong>\r\n    <h4>Prsentation d'un CNEUF</h4>\r\n    <a class=\"btn btn1\" href=\"assets\\C-NEUF-V2.pdf\" download=\"C-NEUF-V2.pdf\"> <i class=\"fas fa-download\"></i> Voir la prsentation</a>\r\n  </div>\r\n</div>\r\n</div>\r\n</div>\r\n<br>\r\n<div  class=\"container\"><h3>Modalit des canditatures </h3>\r\n<div class=\"container bodyuser\">\r\n    <h3>1. CADRE GNRAL ET CONTEXTE DE LAPPEL  PROJETS</h3>\r\n    <p class=\"descrip\">\r\n            Au service de ses membres, lAgence sest efforce au fil du temps de rpondre  leurs\r\n            attentes  travers des activits menes par des implantations de proximit. Parmi celles-ci,\r\n            les Campus numriques francophones (CNF) ont eu un rle novateur majeur.\r\n            Aujourdhui, ces campus sont appels  en cohrence avec les nouvelles orientations\r\n            stratgiques de lAgence -  se renouveler,  reprendre un temps davance sur leur\r\n            cosystme qui doit se matrialiser dans une diversification et une rnovation des services\r\n            quils proposent  nos membres, dans le cadre dune dmarche de co-construction\r\n            partenariale.\r\n            Depuis 2010, un ensemble de plans pour lamlioration et lvolution des services des CNF\r\n            ont t mis en uvre, notamment llaboration dune dmarche qualit ayant permis\r\n            didentifier et de normaliser les services proposs dans les CNF. Sen est suivi un plan\r\n            daction progressif avec des phases de sensibilisation et daccompagnement au\r\n            changement :\r\n        <li>Runion des responsables de CNF Gennevilliers - dcembre 2011</li>\r\n        <li>Runion des responsables de CNF Gennevilliers - janvier 2015</li>\r\n        <li>Corpus : CNF, futurs tiers-lieux des savoirs  dcembre 2015</li>\r\n        <li>Runion des responsables de CNF et des rfrents IFIC  Sofia novembre 2015 </li>\r\n        <li>Diffusion du modle conomique des CNF  janvier 2016</li>\r\n        <li>Cration de la communautd'innovation  avril 2016</li>\r\n        <li>Mise en place du groupe de travail central  juin 2016</li>\r\n\r\n        Le prsent appel  projets donne une suite pratique  ces travaux prparatoires\r\n    </p>\r\n\r\n    <h3>2. LE PROJET C-NEUF</h3>\r\n    <p class=\"descrip\">\r\n            En rsum, le C-NEUF est un projet, men en partenariat avec des tablissements membres\r\n            et des oprateurs socio-conomiques, port par une direction rgionale et mis en uvre par\r\n            une ou plusieurs implantations de proximit. Dans un premier temps, la rponse  lappel \r\n            projet doit maner des CNF.\r\n            Un Projet C-NEUF se pense comme un modle de tiers-lieu acadmique, associatif et\r\n            entrepreneurial, acclrateur dinnovations, facteur de dveloppement et demployabilit. Il\r\n            est compos despaces ouverts aux usagers des services proposs par lAUF et est destin \r\n            accueillir de nouveaux publics. Ces espaces sont dots de ressources numriques, pour la\r\n            formation et la recherche, accessibles  partir despaces collaboratifs (physiques ou virtuels).\r\n            Un C-NEUF est destin :\r\n\r\n        <li>La diffusion/lacquisition des connaissances</li>\r\n        <li>La diffusion/lacquisition des connaissances</li>\r\n        <li>Corpus : CNF, futurs tiers-lieux des savoirs  dcembre 2015</li>\r\n\r\n        <li>La valorisation socio-conomique des savoirs, savoir-faire et des savoir-etre </li>\r\n        Son essence est loffre, dans un contexte dtermin, de services innovants varis en\r\n        cohrence avec les orientations stratgiques de lAUF. Ces services doivent tre clairement\r\n        identifis et leur mise en uvre dcrite avec ses rsultats attendus.\r\n        Une description dtaille figure en annexe au prsent appel\r\n    </p>\r\n\r\n    <h3>3. CONDITIONS DE CANDIDATURE</h3>\r\n    <p class=\"descrip\">\r\n            Le projet doit :\r\n\r\n        <li>tre port et prsent par une direction rgionale</li>\r\n        <li>Fdrer autour dun ou plusieurs CNF au moins trois partenaires :\r\n            <ul>\r\n                <li style=\"list-style-type: circle;\">LAUF </li>\r\n                <li style=\"list-style-type: circle;\">Un partenaire acadmique</li>\r\n                <li style=\"list-style-type: circle;\">Un autre du milieu socio- conomique et/ou associatif</li>\r\n            </ul>\r\n        </li>\r\n        <li>Montrer la participation collective au financement du projet </li>\r\n        <li>Afficher le caractre innovant de la proposition  </li>\r\n        <li>tre dans lalignement des orientations stratgiques de lAUF </li>\r\n        <li>Montrer linsertion de ses activits dans au moins deux des trois espaces\r\n                caractristiques du C-NEUF  </li>\r\n\r\n    </p>\r\n\r\n    <h3>4. MESURES DACCOMPAGNEMENT</h3>\r\n    <p class=\"descrip\">\r\n            Pour le montage des projets, objet de lactuel appel, les directions du numrique des\r\n            partenariats et de la veille-innovation-expertise, pourront conseiller les directions rgionales\r\n            qui les solliciteraient.\r\n            Pour les futurs appels, un sminaire atelier spcifique peut tre organis  la demande des\r\n            directions rgionales qui le souhaitent.\r\n    </p>\r\n\r\n    <h3>5. CRITERES DE SELECTION   </h3>\r\n    <p class=\"descrip\">\r\n          <li>ligibilit : projet port par une direction rgionale et fdrant au moins deux\r\n                partenaires extrieurs  lAUF. La prsence dacteurs socio-conomiques ou\r\n                associatif est demande</li>\r\n          <li>Alignement  la stratgie AUF : rpond  au moins deux des trois dfis de la\r\n                stratgie</li>\r\n          <li>Ancrage territorial : originalit\r\n                locale, spcificit, complmentarit, lisibilit et\r\n                effet structurant</li>\r\n          <li>Faisabilit technique : lenvironnement offre les conditions de ralisation\r\n                favorables</li>\r\n          <li>Engagement des partenaires : engagement de soutien humain, matriel ou\r\n                financier chiffr</li>\r\n          <li>Budget : en adquation avec les objectifs</li>\r\n          <li>Prennisation : explicitation des conditions de prennisation post financement\r\n                initial</li>\r\n    </p>\r\n\r\n    <h3>6. LEMENTS BUDGETAIRES</h3>\r\n    <p class=\"descrip\">\r\n            Il sagit dun projet en partenariat. Il convient de dcrire les apports de la direction rgionale\r\n             sil y a lieu - de chacun des partenaires et de prciser le complment de financement\r\n            demand  la direction du numrique.\r\n\r\n    </p>\r\n\r\n  </div>\r\n\r\n</div>\r\n<div  class=\"container\"><h3>Documents de rfrence </h3>\r\n    <div class=\"container bodyuser\">\r\n    <p class=\"descrip\">\r\n        <li>Pour accder au modle de la charte de qualit : <a href=\"assets/CharteQualiteCNEUF-2019.pdf\" target=\"blank\"> Cliquer ici <i class=\"fas fa-external-link-alt\"></i> </a></li>\r\n        <li>Pour accder au modle de cadre logique : <a href=\"assets/CadreLogiqueCNEUF_Indicatif_2019.pdf\" target=\"blank\"> Cliquer ici <i class=\"fas fa-external-link-alt\"></i> </a></li>\r\n        <li>Pour accder au guide dutilisation de la plateforme : <a href=\"assets/GuidePrliminaireD_usageCNEUF.pdf\" target=\"blank\"> Cliquer ici <i class=\"fas fa-external-link-alt\"></i> </a></li>\r\n    </p>\r\n    </div>\r\n</div>\r\n\r\n\r\n<app-footer></app-footer>\r\n");

